@keyframes rotate360 {
  from {
    transform: translate(-50%)rotate(0);
  }

  to {
    transform: translate(-50%)rotate(360deg);
  }
}

* {
  box-sizing: border-box;
  margin: 0;
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stop-watch {
  border: 5px dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stop-watch__center {
  z-index: 1;
  background-color: #ff0;
  border-radius: 50%;
  width: 5%;
  height: 5%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stop-watch__minute {
  transform-origin: bottom;
  background-color: green;
  width: 10px;
  height: 25%;
  animation: 60s linear infinite rotate360;
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%);
}

.stop-watch__second {
  transform-origin: bottom;
  background-color: red;
  width: 5px;
  height: 50%;
  animation: 5s linear infinite rotate360;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
}

